import { ToastContainer, toast } from 'react-toastify';
import * as actions from '../actions';
import React, { Component } from 'react';
//import { Col, Grid, Row, Alert } from 'react-bootstrap';
import { Route, Redirect,Switch, withRouter } from "react-router-dom";
//import NavMenu from './NavMenu';
import Header from './Header';
import * as appReducer from '../reducers/App';
import * as secReducer from '../reducers/Sec';
import  App  from "./App";
import  AppDef  from "./AppDef";
import  Pricing  from "./Pricing";
import  About  from "./About";
import  Main  from "./Main";
import  Apps  from "./Apps";
import { connect } from 'react-redux';  
import { Menu, Segment, Sticky, Grid, GridColumn, Header as H, Dropdown, Icon } from 'semantic-ui-react'

import 'react-toastify/dist/ReactToastify.css';


class Layout extends Component {
    state = {
      response: ''
    };
  
    componentDidMount() {
      console.log("init:",this.props);
      
    
    }
  
    componentWillUpdate(nextProps) {
  
      console.log(nextProps);
  
    }
 
 
    
    
    render (){   
      return (
      
      <Switch>
                        <Route path='/:orgUriKey/apps' component={Apps} />
                        <Route render={()=>  (
      
      
                      
      <Grid stackable >                          
            <Grid.Row>
              <Grid.Column>

                 <Header/>
              </Grid.Column>

            </Grid.Row>
        <Grid.Row className={'page'} columns={1}> 
        <Grid.Column>
          <ToastContainer autoClose={15000} position={'top-center'} />
           {(this.props.user && this.props.selectedOrg) ?(
                    <div>
                      <Switch>
                        <Redirect path='/null' to="/" />
                        <Route path='/' component={Main} exact/>
                      </Switch>
                      <Route path='/pricing' component={Pricing} />
                      <Route path='/about' component={About} />
                      <Route path='/app/:appUriKey' component={AppDef} />
                    </div>
                  ):(
                    <div>
                      <Switch>
                        <Redirect path='/null' to="/" exact/>
                        <Route path='/:stuff' component={App}/>
                        <Route path='/' component={App} exact/>
                      </Switch>
                      <Route path='/pricing' component={Pricing} exact />
                      <Route path='/about' component={About}  exact  />
                    
                    </div>
                  )
                  
                }
           </Grid.Column>
        </Grid.Row>
      </Grid>
                        )
                      }/>
                      </Switch>

                      
    )
    }


}
const mapStateToProps = (state) => ({
  errors :appReducer.errors(state),
  user : secReducer.user(state),
  selectedOrg: appReducer.selectedOrg(state),

  });
  
  const mapDispatchToProps = (dispatch) => ({
    'clearErrors': (value) => dispatch(actions.globalErrorsClear()),
  });
  
  export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Layout));
