import { AuthenticationContext, adalFetch, withAdalLogin, adalGetToken } from 'react-adal';

import { withGoogleLogin, googleGetToken, googleSecureCall} from './components/react_adal/react-adal-google';

export const adalConfig = {
  tenant: 'common',
  clientId: '9f256bb0-1a93-4247-9e1e-bbd05532e2ce',
  
 endpoints: {
    api: '9f256bb0-1a93-4247-9e1e-bbd05532e2ce',
  },
  cacheLocation: 'localStorage',
  popUp:true
};

export const googleConfig = {
  tenant: null,
  name:'google',
  clientId: '1008335591690-9mgv2poc3vpi0ke1fi7c5m1dl2sejjru.apps.googleusercontent.com',
  authorize: 'auth',
  endpoints: {
    api: 'testApi',
  },
  scope: 'profile email',
  popUp:false,
  navigateToLoginRequestUrl:false,
  instance:'https://accounts.google.com/o/oauth2/v2/',
  cacheLocation: 'localStorage',
 // state:'pass-through value'
};

export const authContext = new AuthenticationContext(adalConfig);

export const adalApiFetch = (fetch, url, options) =>
  adalFetch(authContext, adalConfig.endpoints.api, fetch, url, options);

export const withAdalLoginApi = ()=>{authContext.handleWindowCallback(); return withAdalLogin(authContext, adalConfig.endpoints.api)};

//export const authGoogleContext = new GoogleAuthenticationContext(googleConfig);

/*export const googleApiFetch = (fetch, url, options) =>
  googleFetch(authGoogleContext, '1008335591690-9mgv2poc3vpi0ke1fi7c5m1dl2sejjru.apps.googleusercontent.com', fetch, url, options);
*/
export const withGoogleLoginApi = withGoogleLogin();

export const getGoogleToken = () => googleGetToken();
export const getAdalToken = () => adalGetToken(authContext, adalConfig.endpoints.api);

export const getCachedGoogleUser = () => googleSecureCall(()=>window.gapi.auth2.getAuthInstance().currentUser.get().getBasicProfile());
export const getCachedAdalUser = () => authContext.getCachedUser();

export const logOutGoogle = () => null;
export const logOutAdal = () => authContext.logOut();