import React from 'react';



export function googleGetToken() {
  return new Promise((resolve, reject) => {
    
    googleSecureCall(()=>{resolve(window.gapi.auth2.getAuthInstance().currentUser.get().getAuthResponse().id_token)},
     (e)=>reject(e)
    );
    
    /*authContext.acquireToken(resourceGuiId, (message, token, msg) => {
      if (!msg) resolve(token);
      // eslint-disable-next-line
      else reject({ message, msg });
    });*/
  });
}

