export const googleSecureCall = async (f) => {
  
  let auth = window.gapi.auth2.getAuthInstance();  
  let signedIn = auth.currentUser.get();
  console.log('signedIn:', signedIn.getBasicProfile());
    if (!signedIn){
      let r = await auth.signIn();
       console.log('Google res:, ',r); 
        
      let signedIn = window.gapi.auth2.currentUser.get();
      if (signedIn) {
        return f();
      }
      
    }
    else{
      return f();
    }
  }

