import { handleActions } from 'redux-actions';
import * as actions from '../actions';
import update from 'immutability-helper';
import _ from 'lodash';

const defaultState = {
    test:'',
    errors:[],

    gettingUserOrgs:false,
    userOrgs:[],
    selectedOrg: null,
    enablingOrg:false,
    apps:null,
    
    orgAuthPolicy:null,
    


    
    // Home page
  
    

};
const defaultOrgState = {
    
    apps:null,
    appPage:{org:null, apps:null}
};

export const reducer = handleActions({
    
    [actions.requestUserInfoSuccess]: (state, action) => ({
        ...state,
        user: action.payload,
        
    }),

    [actions.requestUserInfoFailed]: (state, action) => ({
        ...state,
        user: null,
        userOrgs: []
    }),

    [actions.setSecReset]: (state, action) => ({
        ...state,
        user: null,
        userOrgs:[]
    }),

    [actions.globalErrors]: (state, action) => ({
        ...state,
        errors:[...state.errors, action.payload],
        
    }),
    [actions.globalErrorsClear]: (state, action) => ({
        ...state,
        errors:[],
    }),
    [actions.getUserOrgs]: (state, action) => ({
        ...state,
        gettingUserOrgs: true,
    }),
    [actions.setUserOrgs]: (state, action) => {
        let res = {
            ...state,
            gettingUserOrgs: false,
            userOrgs: action.payload,
            selectedOrg: state.user ? _.find(action.payload, i => state.user.organizationKey === i.key) : null
         };
        if (res.selectedOrg){
            res[res.selectedOrg.key] = state[res.selectedOrg.key]||{ ...defaultOrgState };
        }
        return res;

    },
    [actions.selectOrg]: (state, action) => ({
        ...state,
        selectedOrg: action.payload,
        [action.payload.key]: state[action.payload.key] || { ...defaultOrgState }
    }),
    [actions.enableOrg]: (state, action) => {
           return update(state, CreateUpdate(['enablingOrg',true] ));
    },

    [actions.enableOrgOk]: (state, action) => {
        let orgIndex = _.findIndex(state.userOrgs, i => i.id === action.payload.id);
        let updateOrgs = CreateUpdate(['userOrgs',orgIndex,'enabled',true],['selectedOrg','enabled',true],['userOrgs',orgIndex,'name',action.payload.name],['selectedOrg','uriKey',action.payload.uriKey],['enablingOrg',false]);
        return update(state, updateOrgs);
    },

    [actions.setUserOrgsFailed]: (state, action) => ({
        ...state,
        gettingUserOrgs: false,
        userOrgs: [],
        selectedOrg:null
    }),
    [actions.getApps]: (state, action) => ({
        ...state,
        gettingApp: true,
    }),
    [actions.getAppsOk]: (state, action) => {
        let updateApp = CreateUpdate([getOrg(state), 'apps',action.payload.apps],['gettingApp',false]);
        return update(state, updateApp);
    },

    [actions.getAppsFailed]: (state, action) => ({
        ...state,
        gettingApp: false,
    }),
    [actions.saveAppOk]: (state, action) => {
        let appIndex = _.findIndex(state.apps, i => i.id === action.payload.id);
        if (appIndex >= 0){
            let updateApp = CreateUpdate([getOrg(state), 'apps',appIndex,action.payload]);
            return update(state, updateApp);
        }
        else {
            let updateApp = CreateUpdate([getOrg(state), 'apps',[action.payload, ...state.apps]]);
            return update(state, updateApp);
          
        // + delete
      }
    },

      

    [actions.setProperty]: (state, action) => (
        // update(state, {'userOrgs':{[action.payload.index] : {[action.payload.property] : {$set:action.payload.value}}}})
        update(state, action.payload)
    ),
    [actions.setOrgProperty]: (state, action) => (
        update(state, { [getOrg(state)]: action.payload })
    ),

    

}, defaultState);

const getOrg = state => state.selectedOrg ? state.selectedOrg.key : 'unknownOrg';


const getData = state => state.app;
const getDataInContextOfOrg = state => {
    return state.app.selectedOrg ? state.app[state.app.selectedOrg.key] || {} : {} //empty if org is not set
};
export const test = state => getData(state).test;
export const errors = state => getData(state).errors;
export const userOrgs = state => getData(state).userOrgs;
export const enablingOrg = state => getData(state).enablingOrg;
export const selectedOrg = state => getData(state).selectedOrg;
export const apps = state => getDataInContextOfOrg(state).apps;
export const groups = state => getDataInContextOfOrg(state).groups;
export const appPage = state => getDataInContextOfOrg(state).appPage;

export const CreateUpdate = (...args) =>{

    
    let res = {};
  
    for (let index = 0; index < args.length; index++) {
        const element = args[index] ;
        let tmp = res;
        let tmp2 = null;

        for (let i = 0; i < element.length; i++) {
            if (i === element.length -2){
                tmp[element[i]]={$set:element[i+1]};
                break;
            }
            else{
                tmp2 = tmp[element[i]] || {};
                tmp[element[i]]=tmp2;
            }
            
            tmp = tmp2;
        }
    }
    return res;
}

export const CreateAdd = (...args) =>{

    
    let res = {};
  
    for (let index = 0; index < args.length; index++) {
        const element = args[index] ;
        let tmp = res;
        let tmp2 = null;

        for (let i = 0; i < element.length; i++) {
            if (i === element.length -2){
                tmp[element[i]]={$push:[element[i+1]]};
                break;
            }
            else{
                tmp2 = tmp[element[i]] || {};
                tmp[element[i]]=tmp2;
            }
            
            tmp = tmp2;
        }
    }
    return res;
}

export const CreateRemove = (...args) =>{

    
    let res = {};
  
    for (let index = 0; index < args.length; index++) {
        const element = args[index] ;
        let tmp = res;
        let tmp2 = null;

        for (let i = 0; i < element.length; i++) {
            if (i === element.length -2){
                tmp[element[i]]={$splice:[[element[i+1],1]]};
                break;
            }
            else{
                tmp2 = tmp[element[i]] || {};
                tmp[element[i]]=tmp2;
            }
            
            tmp = tmp2;
        }
    }
    return res;
}
