import * as React from 'react';
import { Component } from 'react';
import { Button as BaseButton, ButtonProps, Label as BaseLabel,Icon, Form, Input as BaseInput } from 'semantic-ui-react';
import { Link, NavLink, Redirect,Switch, withRouter } from "react-router-dom";  
import * as uuid from 'uuid';
import AppIcon from './AppIcon';
export class Button extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        const {onClick, text, icon, loading} = this.props;
        return (
          <BaseButton style={{width:'160px'}} loading={loading} basic color="black" icon onClick={onClick}>
            <Icon name={icon}></Icon>{text}
          </BaseButton>      );
    }
    
}

export class Input extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        const {onChange, text, placeholder, value, label} = this.props;
        return (
            <Form.Field>
            <BaseLabel basic pointing="below">{text}</BaseLabel>
            <BaseInput label={label} type='text'  defaultValue={value}
                  placeholder={placeholder}
                  id={null}
                  autoComplete={"nope"}
                  onChange={onChange} />
             </Form.Field>)
    }
    
}

export class AppNavButton extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        const {to, selectedOrg, appInfo} = this.props;
        return (
            <BaseButton basic style={{height:'64px', width:'64px', padding:0}} as={NavLink} to={to}>
            <AppIcon selectedOrg={selectedOrg} appInfo={appInfo}></AppIcon>
          </BaseButton>
        )
    }
    
}


