import { applyMiddleware, combineReducers, compose, createStore } from 'redux';
import thunk from 'redux-thunk';
import { routerReducer, routerMiddleware } from 'react-router-redux';
import * as App from '../reducers/App';
import * as Sec from '../reducers/Sec';
import createSagaMiddleware from 'redux-saga';
import sagas  from '../sagas/index.js';
import { save, load } from "redux-localstorage-simple"

function logger({ getState }) {
  return (next) => (action) => {
    console.log('dispatch:', action)
 
  }
}
export default function configureStore(history) {
  const reducers = {
      app: App.reducer,
      sec: Sec.reducer
    };
    const sageMiddleware = createSagaMiddleware();
  //const sagaMiddleware = applyMiddleware(sagaMiddleware);
  const middleware = [
    thunk,
     routerMiddleware(history),
      sageMiddleware,
     // logger
  ];

  // In development, use the browser's Redux dev tools extension if installed
  const enhancers = [];
  const isDevelopment = process.env.NODE_ENV === 'development';
  if (isDevelopment && typeof window !== 'undefined' && window.devToolsExtension) {
    enhancers.push(window.devToolsExtension());
  }
  
  const rootReducer = combineReducers({
    ...reducers,
      routing: routerReducer
     
  });
  
  const createStoreWithMiddleware 
  = applyMiddleware(
      save({ states: ["sec"], namespace: "apps" }) // Saving done here
  )(createStore)


    let store = createStoreWithMiddleware(
    rootReducer,
    load({ states: ["sec"], namespace: "apps" }),  
       
    compose(applyMiddleware(...middleware), ...enhancers),
    
    );

    sageMiddleware.run(sagas);

    return store;
 
}
