import { ToastContainer, toast } from 'react-toastify';
import {
    put,
    call,
    takeLatest,
    all,
    select
} from 'redux-saga/effects';
import * as api from '../services/api';
import * as actions from '../actions';
import { push } from "react-router-redux";

const parameterIfSet = (p) => {
    if (p && p.code) {
        return p.code;
    }
    return null;
}
const getState = (state) => {
    return {user: state.sec.user, userOrgs: state.app.userOrgs, selectedOrg: state.app.selectedOrg};
    /*
    let request = [
        { key: 'projectCode', value: parameterIfSet(mmt.archiveProject) },
        { key: 'locationFromCode', value: parameterIfSet(mmt.archiveLocationFrom) },
        { key: 'locationToCode', value: parameterIfSet(mmt.archiveLocationTo) },
        { key: 'user', value: parameterIfSet(mmt.archiveUser) },
        { key: 'systemCode', value: parameterIfSet(mmt.archiveSystem) },
        { key: 'skip', value: ((mmt.archiveCurrentPage - 1) * 100) }
    ];
    return request;*/
};

function* signOut(action){
    yield call(api.logOut);
    yield put(actions.setSecReset()); // reset user
    yield put(push('/'));
}

function* requestErrorHandling(failedRequest, actionToFire, payload) {
    failedRequest = failedRequest || {error:"...We don't know what went wrong:("}
    try {
      if (failedRequest.status == 400){
        payload.message = failedRequest.response._bodyText;
        if (actionToFire) { yield put(actionToFire(payload));}
    
      }
      else if (failedRequest.status == 401){
        push('/');
      }
    
      else if (failedRequest.status == 403){
        if (actionToFire) { yield put(actionToFire(payload));}
    
      }
      else{
        if (actionToFire) { yield put(actionToFire(failedRequest));}
        yield* generalError(failedRequest, actionToFire, payload);
      }
    }
      catch(e){
        console.log(e);
      }
    }

function isObject(val) {
    if (val === null) { return false;}
    return ( (typeof val === 'function') || (typeof val === 'object') );
}
function errorMessage(error){
    let msg = error.message || error.error || error;
    if (isObject(msg)){
        return JSON.stringify(msg);
    }
    return msg;
}    
function* generalError(action){

    try{
        console.log("Error!",action.payload)
    
        //  AppInsights.trackException(exception: action.payload);
    
        toast.error(errorMessage(action.payload || action));
    
      
    
    
        // Toast or similar?
    }
    catch (e) {
        console.log(e);
    }
    }
        
function* getUserInfo(action) {

    try {

        var res = yield call(api.getUserInfo);
        if (res && !res.error){
            yield put(actions.setLoginType(res.source));
            yield put(actions.requestUserInfoSuccess(res));
            let state = yield select(getState);
            if (state.userOrgs.length === 0){
                yield* getUserOrgs(action);
            }
        }
        else {
            let state = yield select(getState);
            if (state.user){ // reset user if set
                yield put(actions.requestUserInfoFailed());
                yield put(push('/'));
            }
        }
    } catch (ex) {

        let user = yield select(getState);
        if (user){
            yield put(actions.requestUserInfoFailed());
            yield put(push('/'));
        }

    }
}

function* saveApp(action){
    try {
        let state = yield select(getState);
        if (!state.selectedOrg || !state.selectedOrg.uriKey) return;

        var res = yield call(() => api.saveApp(state.selectedOrg.uriKey, action.payload));
        if (res && !res.error){
            yield put(actions.saveAppOk(res));
        }
        else {
            yield* requestErrorHandling(res, action.addAppFailed, action.payload);
            }
        
    } catch (ex) {

        console.log("ex",ex);
        yield* requestErrorHandling(ex, action.saveAppFailed, action.payload);

    } 
}

function* getApps(action){
    try {
        let state = yield select(getState);
        console.log("state in getApps",state);
        if (!state.selectedOrg || !state.selectedOrg.uriKey) return;
        var res = yield call(() => api.getApps(state.selectedOrg.uriKey));
        if (res && !res.error){
            yield put(actions.getAppsOk(res));
        }
        else {
            yield* requestErrorHandling(res, actions.getAppsFailed, action.payload);
            }
        
    } catch (ex) {

        console.log("ex",ex);
        yield* requestErrorHandling(ex, actions.getAppsFailed, action.payload);

    } 
}

function* enableOrg(action) {

    try {

        var res = yield call(() => api.enableOrg(action.payload));
        if (res && !res.error){
            yield put(actions.enableOrgOk(res));
        }
        else {
            yield* requestErrorHandling(res, null, action.payload);
            }
        
    } catch (ex) {

        console.log("ex",ex);
        yield* requestErrorHandling(ex, null, action.payload);

    }

}

function* getUserOrgs(action) {

    try {

        var res = yield call(api.getUserOrgs);
        if (res && !res.error){
            yield put(actions.setUserOrgs(res));
        }
        else {
            yield* requestErrorHandling(res, actions.setUserOrgsFailed, action.payload);
            }
        
    } catch (ex) {

        yield* requestErrorHandling(ex, actions.setUserOrgsFailed, action.payload);

    }

}
const root = function* rootSaga() {

    yield all([
        takeLatest(`${actions.requestUserInfo}`, getUserInfo),
        takeLatest(`${actions.signOut}`, signOut),
        takeLatest(`${actions.getUserOrgs}`, getUserOrgs),
        takeLatest(`${actions.enableOrg}`, enableOrg),
        takeLatest(`${actions.getApps}`, getApps),
      //  takeLatest(`${actions.saveApp}`, saveApp),
        
    ]);

};

export default root;
