const mapStateToProps = (state) => ({
  user : reducer.user(state),
  loggedIn : reducer.loggedIn(state),
  userOrgs: appReducer.userOrgs(state),
  selectedOrg: appReducer.selectedOrg(state)
  
});

const mapDispatchToProps = (dispatch) => ({
  'signOut': (value) => dispatch(actions.signOut()),
  'setProperty': (v) => dispatch(actions.setProperty(v)),
  'selectOrg':(v)=> dispatch(actions.selectOrg(v))

 
});


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header));
