import { connect } from 'react-redux';
import React, { Component } from 'react';
import logo from '../logo.svg';
import './App.css';
import * as api from '../services/api';
import config from '../config.json';
import * as actions from '../actions';
import * as reducer from '../reducers/Sec';
import * as appReducer from '../reducers/App';
import { Link} from "react-router-dom";
import FileButton from './sub/FileButton';
import { Button, Segment, Grid, Radio, Header, Loader, Icon, Form, Label, Input, Divider } from 'semantic-ui-react';
import update from 'immutability-helper';
import Dropzone from 'react-dropzone'


import ReactIframeResizer from 'react-iframe-resizer-super';

const iframeResizerOptions = { checkOrigin: false };
class AppDef extends Component {
    constructor(props, context) {
        super(props, context);
    
        this.state = {
        };
    }

   componentDidMount() {
    this.setState({appUriKey:this.props.match.params.appUriKey,loading:true},()=>this.loadApp());
    //this.loadApp();

    
  }

  componentDidUpdate(prevProps) {
    if (!this.state.loading && this.props.match.params.appUriKey !== prevProps.match.params.appUriKey){
        this.setState({loading:true}, ()=> this.loadApp());
    }
  }

  loadApp(){
    if (!this.state.app || (this.props.selectedOrg && this.state.appUriKey)){
      this.setState({loading:true}, async ()=>
      
          {let app = await api.getApp(this.props.selectedOrg.uriKey, this.state.appUriKey)
      
          if (app.error){ 
              console.log('error:: ', app.error);
          }
          else{
              this.setState({app:app});
          }
          this.setState({loading:false});
    });
      
    }
  }

  loadVersions(slot){
    
      this.setState({loadingVersions:true}, async ()=>
      {
      try
      {
            {
              let versions = await api.getVersions(this.props.selectedOrg.uriKey, this.state.appUriKey, slot.name)
        
            if (versions.error){ 
                console.log('error:: ', versions.error);
            }
            else{
                this.setState({versions:{slot:slot.name, versions:versions }});
            }
          }
      }
      finally{
        this.setState({loadingVersions:false});
      }
    }
    );
      
  }

  

handleIconChoiceChange = (e, { value }) => {
console.log('update',update, 'state:',this.state,'value:', value, 'this.setState:', this.setState)
let u = update(this.state, appReducer.CreateUpdate(['versionToSave','iconChoice',value]));
console.log(u);    
this.setState(u);
}

handleBundleChange = (e, { value }) => {
  this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','bundleChoice',value])));

}
 renderBundleChoice(){
   switch(this.state.versionToSave.bundleChoice){
     case "redirect": return  (<div class="field">
     <label>Redirect</label>
     <Input type="text" value={this.state.versionToSave.redirect} onChange={(e)=>this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','redirect',e.target.value])))} name="redirect" placeholder="http://my-webpage.com"/>
   </div>)
     case "upload": return  (<div class="field" style={{borderStyle:'dashed', borderWidth:1, padding:'8px'}}>
                      <label>Bundle <i style={{marginLeft:'8px',color:'#aaa'}}>a zipped bundle must at least contain an index.html-file at the root</i></label>
                      <Dropzone multible={false}
                          onDrop={files => {if (files.length === 1) this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','bundle',files[0]])))}}
                          disableClick accept=".zip"
                        >
                          {({getRootProps, getInputProps, open}) => (
                            <div {...getRootProps()}>
                              <input {...getInputProps()} />
                                <p>Drop .zip file here</p>
                        ​
                                <Button basic type="button" onClick={() => open()}>
                                  {this.state.versionToSave.bundle? this.state.versionToSave.bundle.name : 'Choose file'}
                                </Button>
                            </div>
                          )}
                        </Dropzone>
                  </div>)
      default:return null;
   }
}
 
renderIconChoice(){
  switch(this.state.versionToSave.iconChoice){
    case "relative": return  (<div class="field">
                       <label>Relative path</label>
                       <Input type="text" name="path" value={this.state.versionToSave.iconPath} onChange={(e)=>this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','iconPath',e.target.value])))} placeholder="/resources/logo.svg"/>
                   </div>)
    case "static": return  (<div class="field">
                     <label>Url to icon</label>
                     <Input type="text" name="path" value={this.state.versionToSave.iconPath} onChange={(e)=>this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','iconPath',e.target.value])))} placeholder="http://your-resource-server.com/logo.svg"/>
                 </div>)
     case "upload":return  (<div class="field" style={{borderStyle:'dashed', borderWidth:1, padding:'8px'}}>
                 <label>Upload .svg or .png <i style={{marginLeft:'8px',color:'#aaa'}}>we recommend .svg for best results</i></label>
                 <Dropzone multible={false}
                          onDrop={files => {if (files.length === 1) this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','icon',files[0]])))}}
                          disableClick accept=".svg,.png"
                        >
                          {({getRootProps, getInputProps, open}) => (
                            <div {...getRootProps()}>
                              <input {...getInputProps()} />
                                <p>Drop .svg or .png file here</p>
                        ​
                                <Button basic type="button" onClick={() => open()}>
                                  {this.state.versionToSave.icon? this.state.versionToSave.icon.name : 'Choose file'}
                                </Button>
                            </div>
                          )}
                        </Dropzone>
             </div>)
     default:return null;
  }
}

  canBeSaved(version){
    return (
      (version.iconChoice === 'upload' && version.icon) || 
      ((version.iconChoice=== "redirect" || version.iconChoice=== "static") && version.iconPath)  &&
      (version.redirect  || version.bundle) &&
      version.version
    )
  }
  renderNewVersion(slot){
    return (this.state.newVersion && slot ?(<div>
      <Header>New version</Header>
      <form class="ui form">
        <div class="field">
          <label>Version</label>
          <Input type="text" value={this.state.versionToSave.version} onChange={(e)=>this.setState(update(this.state, appReducer.CreateUpdate(['versionToSave','version',e.target.value])))} name="version" placeholder="1.0.0"/>
        </div>
        <div class="field">
        <label>Bundle</label>
        <div class="two fields">
       
          
        <Form.Field>
          <Radio
            label='Upload bundle'
            name='bundle'
            value='upload'
            checked={this.state.versionToSave.bundleChoice === 'upload'}
            onChange={this.handleBundleChange}
          />
          </Form.Field>
          <Form.Field>
          <Radio
            label='Redirect to another web page'
            name='bundle'
            value='redirect'
            checked={this.state.versionToSave.bundleChoice === 'redirect'}
            onChange={this.handleBundleChange}
          />
        </Form.Field>
          </div>
          {this.renderBundleChoice()}
        
       </div> 
      
        <div class="field">
        <label>Icon</label>
        <div class="two fields">
       
        <Form.Field>
          <Radio
            label='Use icon from package'
            name='radioGroup'
            value='relative'
            checked={this.state.versionToSave.iconChoice === 'relative'}
            onChange={this.handleIconChoiceChange}
          />
        </Form.Field>
        <Form.Field>
          <Radio
            label='Use icon from other resource'
            name='radioGroup'
            value='static'
            checked={this.state.versionToSave.iconChoice === 'static'}
            onChange={this.handleIconChoiceChange}
          />
        </Form.Field>
        <Form.Field>
          <Radio
            label='Upload new icon'
            name='radioGroup'
            value='upload'
            checked={this.state.versionToSave.iconChoice === 'upload'}
            onChange={this.handleIconChoiceChange}
          />
        </Form.Field>
        <Form.Field>
          <Radio
            label='Use icon from last version'
            name='radioGroup'
            value='last'
            checked={this.state.versionToSave.iconChoice === 'last'}
            onChange={this.handleIconChoiceChange}
          />
        </Form.Field>

       </div>
       {this.renderIconChoice()}
       </div>
       
        
          
        
        <Button disabled={!this.canBeSaved(this.state.versionToSave)} loading={this.state.saving} onClick={(e)=>{e.preventDefault(); console.log('saving',this.state.versionToSave); this.saveNewVersion(slot)}} basic color="black">Save</Button>
        <Button onClick={()=>this.setState({newVersion:false})} basic color="black">Cancel</Button>
    
    </form>
      </div>): 
    <Button onClick={()=>this.setState({newVersion:true, versionToSave:{iconChoice:'upload', bundleChoice:'upload'}})} basic color="black">New Version</Button>)
  }

  async saveNewVersion(slot){
    this.setState({saving:true});
    try{
      let res = await api.saveVersion(this.props.selectedOrg.uriKey, this.state.appUriKey, slot, this.state.versionToSave);
      if (res && !res.error){
        if (slot === 'Production')
        {
          await this.loadApp(slot)

        }
        await this.loadVersions(slot);
      }
    }
    finally{
      this.setState({saving:false});
    
    }
  }
  renderVersions(){
    const versions = this.state.versions;
    return (
        this.state.loadingVersions ? <Loader/> : (
        versions ? (
          <div style={{marginTop:'24px'}}>
            <Header>{versions.slot}</Header>
            {this.renderNewVersion(versions.slot)}
            

              {versions.versions.map((v, i)=><Button>{v.name}</Button>)}
          </div>
          )  :null
        )
      )
  }

  renderSlots(){
    const slots = this.state.app.slots;
    return (
      <div>
        <Header style={{marginTop:'24px'}}>Slots</Header>
        {slots.map((s, i) => <Button onClick={()=>this.loadVersions(s)} basic style={{lineHeight:'1.6'}}>{s.name}<br/><span><i>{s.latestVersion == null ?'Non deployed':'v.'+s.latestVersion}</i></span></Button>)}
      </div>
    )
  }
  render() {
    return (
      <div>
        {this.state.app? (<div><h3>{this.state.app.name}</h3>
        {this.state.loading ? null : (
          <ReactIframeResizer style={{height:'128px', width:'128px'}} iframeResizerOptions={iframeResizerOptions}>
            
              <img width={100} height={100}  style={{marginLeft:'-0px' ,marginRight:'-0px'}} src={`${config.apiBase}/api/v0/Organization/${this.props.selectedOrg.uriKey}/apps/${this.state.app.uriKey}/icon`}/>
            
          </ReactIframeResizer>
        )}
        {this.renderSlots()}
        {this.renderVersions()}
        </div>) : null}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
 selectedOrg: appReducer.selectedOrg(state),

  
});

const mapDispatchToProps = (dispatch) => ({
 // 'setLoginType': (value) => dispatch(actions.setLoginType(value)),
  
});

export default connect(mapStateToProps, mapDispatchToProps)(AppDef);

