import React, { Component } from "react";
import config from '../../config.json';
import ReactIframeResizer from 'react-iframe-resizer-super';

const iframeResizerOptions = { checkOrigin: false };

export default class AppIcon extends Component {

    shouldComponentUpdate(nextProps) {
        const {appInfo } = this.props;
       
        if(nextProps.appInfo && nextProps.appInfo.updated != appInfo.updated) {
            return true;
        }

        return false;
    }

    render()
    {
        const { appInfo, selectedOrg } = this.props;
    

        return(<div> 
        <div style={{height:'64px', width:'64px',position:'absolute'}}></div>
        <ReactIframeResizer style={{height:'64px', width:'64px'}} iframeResizerOptions={iframeResizerOptions}>
          
            <img width={50} height={50}  style={{marginLeft:'-0px' ,marginRight:'-0px'}} src={`${config.apiBase}/api/v0/Organization/${selectedOrg.uriKey}/apps/${appInfo.uriKey}/icon`}/>
          
        </ReactIframeResizer>
        <span style={{ fontWeight:'bold', padding:'4px', borderRadius:'8px'}}>{appInfo.name}</span>
        </div>)
    }
}