import { connect } from 'react-redux';
import React, { Component } from 'react';
import logo from '../logo.svg';
import './App.css';
import * as api from '../services/api';
import config from '../config.json';
import * as actions from '../actions';
import * as reducer from '../reducers/Sec';
import * as appReducer from '../reducers/App';
import { Segment, Grid, GridColumn, Header, Dropdown, Icon, Form, Label,  Divider } from 'semantic-ui-react'
import {Button, Input, AppNavButton} from './sub/Components';
import { Link, NavLink, Redirect,Switch, withRouter } from "react-router-dom";
import Iframe from 'react-iframe';
import AppIcon from './sub/AppIcon';



class Main extends Component {
  state = {
    response: ''
  };

  componentDidMount() {
    console.log("init:",this.props);
    if ((!this.props.apps || !this.props.apps.map)) {
      this.props.getApps();
      this.loadGroups();
    }
    
  }

  componentWillUpdate(nextProps) {
    if ( (this.props.selectedOrg && nextProps.selectedOrg) && ((this.props.selectedOrg.id != nextProps.selectedOrg.id) || (this.props.selectedOrg.enabled != nextProps.selectedOrg.enabled))){
      this.props.getApps();
      this.loadGroups();
    }
    console.log('Main: ',nextProps);

  }

  async saveApp(app, index){
    this.props.setOrgProperty(appReducer.CreateUpdate(['apps',index,'saving',true],['apps',index,'error',null]));
    let res = app;
    try{
      res = await api.saveApp(this.props.selectedOrg.uriKey, app);
      if (res.error){
        this.props.setOrgProperty(appReducer.CreateUpdate(['apps',index,'saving',false,],['apps',index,'error',res.error]));
      }
      else{
        this.props.setOrgProperty(appReducer.CreateUpdate(['apps',index,'saving',false,],['apps',index,res]));
      }
    }
    finally{
      this.props.setOrgProperty(appReducer.CreateUpdate(['apps',index,'saving',true]));
    }
  }

  init(org){
     return (
      <Form>
      <Form.Field>
        <Input value={org.name}
              placeholder="Organization name"
             text="Your organization is unknown to us. Please enter the correct name of this organization"
             onChange={e => this.props.setProperty(appReducer.CreateUpdate(['selectedOrg','name', e.target.value]))}/>
      </Form.Field>
      <Button onClick={() => this.props.enablingOrg ? null : this.props.enableOrg(org)} loading={this.props.enablingOrg} text={this.props.enablingOrg ? 'Enabling':'Ok'}/>
    </Form>
)
  }

  
  firstUser(org){
    // Wait until we know number of users
    
    return [<p>You are the first to join <i>{org.name}</i>, welcome!</p>,
    <p>Since you are the first to join, you will have administrator rights to this account. You can add other user and distribute rights as you see fit</p>,
    <p>{org.name} is added under the <Link to="/pricing">free subscription</Link> which is limited to up to 20 users and 10 apps. Go to <Link to="/account">account administration</Link> to administer users and account information<br/><br/></p>
    ]
  }

  personal(org){
    return <p>This is your personal account. You can use this for testing</p>
  }
  guest(org){
    return <p>You are are invited as a member of this account</p>
  }

  newApp(item, i){
    return (
      <Form>
      <Grid stackable style={{marginTop:'0px', marginLeft:'0px'}}>
    <Grid.Row columns={3} >
        <Grid.Column>
        <Form.Field>
        <Input text="Application name"
               value={item.name}
              placeholder="App name"
              onChange={e => this.props.setOrgProperty(appReducer.CreateUpdate(['apps',i,'name', e.target.value]))} />
      </Form.Field>
    </Grid.Column>
    <Grid.Column>
    <Form.Field>
      <Input  label={this.props.selectedOrg.uriKey+'-'} value={item.uriKey}
             placeholder="uri" text="App URI"
             onChange={e => this.props.setOrgProperty(appReducer.CreateUpdate(['apps',i,'uriKey', e.target.value]))} />
      
    </Form.Field>
    </Grid.Column>
    <Grid.Column>
      <Form.Field>
        <div style={{marginTop:'40px'}}></div>
        <Button loading={item.saving} onClick={() => this.saveApp(item, i)} icon="save" text="Save"/>
        <Button onClick={()=>this.props.setOrgProperty(appReducer.CreateRemove(['apps',i]))} icon="cancel" text="Cancel"/>  
        </Form.Field>
       </Grid.Column>
    </Grid.Row>
    </Grid>
    </Form>
     
   
    )
  }

  oldApp(item,i){
    return (
      <AppNavButton to={'/app/'+item.uriKey} selectedOrg={this.props.selectedOrg} appInfo={item}/>
      
    )
  }
  apps(){
    return (<div>
      <Header>Apps</Header>
      <Button icon="plus" text="Add app" onClick={()=>this.props.setOrgProperty(appReducer.CreateAdd(['apps',{name:'New app',newApp:true}]))}/>
      <Grid style={{marginTop:'16px', marginLeft:'0px'}}>
        {this.props.apps.map((item, i)=>(
            item.newApp ? (<Grid.Row column={1}>
                              <Grid.Column>{this.newApp(item,i)}</Grid.Column>
                            </Grid.Row>):null
         ))}
     
        <Grid.Row columns={8}>
          {this.props.apps.map((item, i)=>(
            !item.newApp ?<Grid.Column>{this.oldApp(item,i)}</Grid.Column>:null
          ))}
        </Grid.Row>
      </Grid>

      
      
      
      
    </div>)
  }
  loadGroups(){
    
    this.setState({loadingGroups:true}, async ()=>
    {
    try
    {
          {
            let groups = await api.getOrgGroups(this.props.selectedOrg.uriKey)
      
          if (groups.error){ 
              console.log('group error:: ', groups.error);
          }
          else{
            this.props.setOrgProperty(appReducer.CreateUpdate(['groups',groups]));
          }
        }
    }
    finally{
      this.setState({loadingGroups:false});
    }
  }
  );
    
}

 async saveGroup(group, index){
        this.props.setOrgProperty(appReducer.CreateUpdate(['groups', index, 'saving', true]));
        let saved = await api.saveOrgGroup(this.props.selectedOrg.uriKey, group);
        if (saved.error) {
          this.props.setOrgProperty(appReducer.CreateUpdate(['groups', index, 'saving', false], ['groups', index, 'error', saved.error]));
        }
        else {
          this.props.setOrgProperty(appReducer.CreateUpdate(['groups', index, saved]));
        }
 }

 async deleteGroup(group, index){
   if (group.isNew){
      this.props.setOrgProperty(appReducer.CreateRemove(['groups', index]));
   }
   else{
      this.props.setOrgProperty(appReducer.CreateUpdate(['groups', index, 'saving', true]));
      let saved = await api.deleteOrgGroup(this.props.selectedOrg.uriKey, group);
      if (saved.error) {
        this.props.setOrgProperty(appReducer.CreateUpdate(['groups', index, 'saving', false], ['groups', index, 'error', saved.error]));
      }
      else  {
        this.props.setOrgProperty(appReducer.CreateRemove(['groups', index]));
      }
  }
 }

 cancelEditGroup(group, index){
  if (group.new) {
    let newStateUpdate = appReducer.CreateRemove(['groups', index]);
    this.props.setOrgProperty(newStateUpdate);
    
}
else {
    let newStateUpdate = appReducer.CreateUpdate(['groups', index, { ...group.original }]);
    this.props.setOrgProperty(newStateUpdate);
}
 }

 editGroup(group, index){
  let newStateUpdate = appReducer.CreateUpdate(['groups', index, 'edit', true], ['groups', index, 'original', { ...group }]);
  this.props.setOrgProperty(newStateUpdate);

 }

 
 showAddUserToGroup(group, index){
    appReducer.CreateUpdate(['groups', index, 'showAddUser', true]);
 }
 async searchForUsers(filter){
   let users = await api.getUsers(this.props.selectedOrg, null, filter);
   return users;

 }
 renderAddUser(group, index){
   return <Form>
         <Input onChange={e => this.props.setOrgProperty(appReducer.CreateUpdate(['groups',index,'name', e.target.value]))} text="Group Name" placeholder="Group name" value={group.name}/>
       
   </Form>
 }

  renderGroup(group, index){
    return (<Form>
    <Grid stackable style={{marginTop:'0px', marginLeft:'0px'}}>
  <Grid.Row columns={2} >
      <Grid.Column>
      {group.edit?(
         <Form.Field>
           <Input onChange={e => this.props.setOrgProperty(appReducer.CreateUpdate(['groups',index,'name', e.target.value]))} text="Group Name" placeholder="Group name" value={group.name}/>
           <Button loading={group.saving} onClick={() => this.showAddUserToGroup(group, index)} text="Add user(s) to group" icon="plus"/>

         </Form.Field>
      ):(
        <Form.Field>
          <span className={'ui input'} style={{paddingTop:'32px'}}>{group.name + ((group.userCount === undefined) ? null:` (${group.userCount} users)`)}</span>
          
        </Form.Field>
      )
      }
    
  </Grid.Column>
  <Grid.Column>
    <Form.Field>
      <div style={{marginTop:'40px'}}></div>
      {group.edit ?(
      <div>
      <Button  loading={group.saving} onClick={() => this.saveGroup(group, index)} text="Save" icon="save"/>
      <Button loading={group.saving}  onClick={()=>this.cancelEditGroup(group, index)} text="Cancel" icon="cancel"/>
      </div>
      ):(
        <div>
          <Button loading={group.saving} onClick={() => this.editGroup(group, index)} text="Edit" icon="edit"/>
          <Button loading={group.saving}  onClick={()=>this.deleteGroup(group, index)} text="Delete" icon="remove"/>
        </div>
          
      )}
      </Form.Field>
     </Grid.Column>
  </Grid.Row>
  </Grid>
  </Form>)
   
 
  }
 
  renderGroups(){
   return this.props.groups ?(
      <div>
      <Button icon="plus" text="Add group" onClick={()=>this.props.setOrgProperty(appReducer.CreateAdd(['groups',{name:'New group',isNew:true, edit:true}]))}/>
      <Grid style={{marginTop:'16px', marginLeft:'0px'}}>
        {this.props.groups.map((item, i)=>(
             <Grid.Row column={1}>
                              <Grid.Column>{this.renderGroup(item,i)}</Grid.Column>
                            </Grid.Row>))
         }
      </Grid>
       </div>
     )
     :<span>No groups:(</span>
 }

  main(){
    console.log('apps:',this.props.apps);
   return [this.props.apps && this.props.apps.map ?this.apps() :null,
    <div>
    <h4>Groups</h4>
     {this.state.loadingGroups ? <span>Loading...</span>:this.renderGroups()}

     </div>

  ]

  }

  render() {
    return (
      <div>
        {(this.props.userOrgs.length > 0 && this.props.selectedOrg !== null) ?(
          <div>
            {[(!this.props.selectedOrg.enabled ? this.init(this.props.selectedOrg): 
            (this.props.selectedOrg.numberOfUsers === 1 && !this.props.selectedOrg.personal ? this.firstUser(this.props.selectedOrg) : null)),
            ((this.props.selectedOrg.personal && this.props.selectedOrg.owner && this.props.selectedOrg.owner.key === this.props.user.key ? this.personal(this.props.selectedOrg) : null)),
            ((this.props.selectedOrg.personal && this.props.selectedOrg.owner && this.props.selectedOrg.owner.key !== this.props.user.key ? this.personal(this.props.selectedOrg) : null)),
            this.props.selectedOrg.enabled ? this.main():null]}
          </div>
        )
          
        :null}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  loginType : reducer.loginType(state),
  user : reducer.user(state),
  userOrgs: appReducer.userOrgs(state),
  selectedOrg: appReducer.selectedOrg(state),
  enablingOrg: appReducer.enablingOrg(state),
  apps: appReducer.apps(state),
  groups: appReducer.groups(state)
  
});

const mapDispatchToProps = (dispatch) => ({
  'setLoginType': (value) => dispatch(actions.setLoginType(value)),
  'requestUserInfo': (value) => dispatch(actions.requestUserInfo()),
  'enableOrg': (v) => dispatch(actions.enableOrg(v)),
  'setProperty': (v) => dispatch(actions.setProperty(v)),
  'setOrgProperty': (v) => dispatch(actions.setOrgProperty(v)),
  'enableOrg':(v) => dispatch(actions.enableOrg(v)),
  'saveApp':(v) => dispatch(actions.saveApp(v)),
  'getApps':(v) => dispatch(actions.getApps(v)),
  
});

export default connect(mapStateToProps, mapDispatchToProps)(Main);

