import { handleActions } from 'redux-actions';
import * as actions from '../actions';

const defaultState = {
    // Home page
    loggedIn: false,
    loginType: null,
    user: null,
    token: null
    
    

};

export const reducer = handleActions({
    [actions.setLoginType]: (state, action) => ({
        ...state,
        loginType:action.payload,
        loginFailed : false,
    }),
   
    [actions.requestUserInfoSuccess]: (state, action) => ({
        ...state,
        loggedIn: true,
        user: action.payload,
        loginFailed : false,
        
    }),

    [actions.requestUserInfoFailed]: (state, action) => ({
        ...state,
        loggedIn: false,
        user: null,
        loginFailed:true
        
    }),

    [actions.setSecReset]: (state, action) => ({
        ...state,
        loginType:'',
        loggedIn: false,
        user: null
    }),

}, defaultState);

const getData = state => state.app;

const getSec = state => state.sec;

export const loginType = state => getSec(state).loginType;
export const loggedIn = state => getSec(state).loggedIn;
export const user = state => getSec(state).user;
